body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  /* font-family: 'Quicksand', sans-serif;
  font-weight: 300; */
}

/* Use z-index of other components to make particles visible or not
Note: z-index only works on positioned elements (position: absolute, 
position: relative, position: fixed, or position: sticky).
*/
.particles-position {
  /* background-color: red; */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.above-particles {
  position: relative;
  z-index: 1;
}

/* Social networks */
.social-networks {
  padding-top: 20px;
}

.social-network-icon {
  color: white;
  padding-left: 5px;
  padding-right: 5px;
  margin: 4px;
  transition: all 0.5s;
  position: relative;
  z-index: 1;
}

.fa-linkedin:hover {
  color: #2867b2;
  text-decoration: none;
}

.fa-github:hover {
  color: #6e5494;
  color: #767676;
  text-decoration: none;
}

.fa-instagram {
  background: radial-gradient(
    circle at 30% 107%,
    #fdf497 0%,
    #fdf497 5%,
    #fd5949 45%,
    #d6249f 60%,
    #285aeb 90%
  );
  background: -webkit-radial-gradient(
    circle at 30% 107%,
    #fdf497 0%,
    #fdf497 5%,
    #fd5949 45%,
    #d6249f 60%,
    #285aeb 90%
  );
  background-clip: text;
  -webkit-background-clip: text;
}

.fa-instagram:hover {
  color: transparent;
  text-decoration: none;
}

.fa-envelope:hover {
  color: #f1d592;
  text-decoration: none;
}

/* Overall header */
.App-header {
  background-color: #000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* Headline */
.my-name {
  font-size: 4.2rem;
}

.my-picture {
  max-height: 75%;
  max-width: 75%;
  position: relative;
  z-index: 1;
}

.black-navbar {
  background-color: black;
}

.navbar-title {
  font-size: 1.5rem;
  text-decoration: none !important;
}

.navbar-option,
.navbar-title {
  color: rgba(255, 255, 255, 0.5) !important;
}

.navbar-option:hover,
.navbar-title:hover {
  color: rgba(255, 255, 255, 0.75) !important;
}

.my-active .navbar-title {
  color: #fff !important;
}

.my-active .nav-link {
  border-bottom: 1px solid #fff;
  color: #fff !important;
}

#about {
  background-color: #fbfbfb;
}

.title {
  font-size: 3rem;
}

.description {
  text-align: justify;
}

.label {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 4px 8px;
}

#experience {
  background-color: #f0ece2;
}

.experience-title {
  font-size: 3.3rem;
}

.job-heading {
  font-size: 2.3rem;
}

@media (max-width: 767px) {
  .job-heading {
    text-align: center;
  }
}

.job-subheading {
  font-size: 1.3rem;
}

.job-description {
  text-align: left;
}

#projects {
  background-color: #fff;
}

.title {
  font-size: 3rem;
}

.project-list {
  text-align: justify;
}

#accolades {
  background-color: #f0ece2;
}

.title {
  font-size: 3rem;
}

.accolade-list {
  text-align: left;
}

#contact {
  background-color: black;
}

.contact-title {
  color: white;
  font-size: 2.5rem;
}

.copyright {
  color: white;
  font-size: 1rem;
}

